@font-face {
  font-family: "Oswald";
  src: url("../../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../../fonts/oswald/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../../fonts/oswald/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../../fonts/lato/Lato-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  margin: 0 auto;
  height: 100%;
}

img {
  max-width: 100%;
  object-fit: contain;
  object-position: center;
}

.button {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  padding: 10px 0;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}

.title--base {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  margin: 0;
}
@media (min-width: 768px) {
  .title--base {
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    font-size: 60px;
    line-height: 60px;
    color: #000000;
  }
}
.title--header {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  color: #000000;
  text-decoration: none;
  list-style: none;
}
.title--mini {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  list-style: none;
  margin: 0;
}
@media (min-width: 768px) {
  .title--mini {
    font-size: 16px;
    line-height: 24px;
  }
}
.title--info {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
  color: #000000;
  background-color: #ffffff;
  position: relative;
}
.title--info::before {
  content: "";
  position: absolute;
  top: 55%;
  transform: translateY(-50%);
  width: 100vw;
  height: 2px;
  background-color: #68b738;
  z-index: -1;
  left: 0;
}
@media (min-width: 768px) {
  .title--info::before {
    content: "";
    left: -30px;
  }
}
@media (min-width: 1440px) {
  .title--info::before {
    content: "";
    left: -108px;
  }
}
@media (min-width: 768px) {
  .title--info {
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    font-size: 36px;
    line-height: 36px;
  }
}
.title--text {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
}
@media (min-width: 768px) {
  .title--text {
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    font-size: 60px;
    line-height: 60px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.map__interactive {
  display: block;
  border: none;
}

.social__link--vkontakte {
  display: block;
  mask-image: url("../../img/sprite.svg#vk");
  background-color: #666666;
  width: 28px;
  height: 22px;
}
.social__link--youtube {
  display: block;
  mask-image: url("../../img/sprite.svg#youtube");
  background-color: #666666;
  width: 28px;
  height: 22px;
}
.social__link--telegram {
  display: block;
  mask-image: url("../../img/sprite.svg#telegram");
  background-color: #666666;
  width: 28px;
  height: 22px;
}
.social__link:hover, .social__link:focus-visible {
  background-color: #68b738;
}
.social__link:active {
  background-color: #68b738;
  opacity: 0.3;
}

.page-header {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .page-header {
    flex-direction: row;
    padding: 0 30px;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .page-header {
    padding: 53px 110px 0 110px;
    background-color: transparent;
  }
}

.page-header__logo-container {
  display: flex;
  justify-content: space-between;
  padding: 10px 20px 17px;
}

.page-header__logo img {
  height: 38px;
}
@media (min-width: 768px) {
  .page-header__logo img {
    width: 174px;
    height: 52px;
  }
}
@media (min-width: 1440px) {
  .page-header__logo img {
    width: 202px;
    height: 59px;
  }
}

@media (min-width: 768px) {
  .page-header__logo {
    align-items: center;
    display: flex;
  }
}
.page-header__logo:hover {
  opacity: 0.8;
}
.page-header__logo:active {
  opacity: 0.6;
}

.page-header__logo-toggle {
  border: none;
  padding: 0;
  height: 38px;
  width: 28px;
  margin-left: auto;
  background-color: #ffffff;
  cursor: pointer;
}
@media (min-width: 768px) {
  .page-header__logo-toggle {
    display: none;
  }
}

.page-header--closed .page-header__logo-toggle::before {
  content: "";
  position: absolute;
  top: 24px;
  right: 20px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.page-header--opened .page-header__logo-toggle::before, .page-header--opened .page-header__logo-toggle::after {
  content: "";
  position: absolute;
  top: 30px;
  right: 20px;
  width: 28px;
  height: 2px;
  background-color: #000000;
}
.page-header--opened .page-header__logo-toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.page-header--opened .page-header__logo-toggle::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .page-header--closed .site-nav__item {
    display: none;
  }
}
@media (max-width: 767px) {
  .page-header--opened .site-nav {
    position: absolute;
    top: 65px;
    left: 0;
    width: 100%;
    background-color: #ffffff;
    z-index: 2;
  }
}
.page-header--nojs .page-header__logo-toggle {
  display: none;
}

.page-header--nojs .site-nav__list {
  display: block;
  position: static;
  min-height: 0;
}

.page-header--nojs .site-nav__item {
  display: block;
}

.site-nav__item {
  text-transform: uppercase;
  text-align: center;
  padding-top: 22px;
  padding-bottom: 24px;
  border-top: 1px solid #e6e6e6;
}
.site-nav__item:hover {
  opacity: 0.6;
}
.site-nav__item:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .site-nav__item {
    border: none;
  }
}

.site-nav__link {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
