    border-bottom: 2px solid #ffffff;
  }
}

@media (min-width: 768px) {
  .site-nav__list {
    display: flex;
    flex-wrap: wrap;
    width: 440px;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
