.title {
  &--base {
    @include font("Oswald", 400, 36px, 36px);
    margin: 0;

    @media (min-width: $tablet-width) {
      @include font("Oswald", 400, 60px, 60px);
      color: $color-black;
    }
  }

  &--header {
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    text-transform: uppercase;
    color: $color-black;
    text-decoration: none;
    list-style: none;
  }

  &--mini {
    @include font("Lato", 400, 14px, 18px);
    list-style: none;
    margin: 0;

    @media (min-width: $tablet-width) {
      font-size: 16px;
      line-height: 24px;
    }
  }

  &--info {
    @include font("Oswald", 400, 24px, 26px);
    color: $color-black;
    background-color: $color-white;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 55%;
      transform: translateY(-50%);
      width: 100vw;
      height: 2px;
      background-color: $color-green;
      z-index: -1;
      left: 0;
    }

    @media (min-width: $tablet-width) {
      &::before {
        content: "";
        left: -30px;
      }
    }

    @media (min-width: $desktop-width) {
      &::before {
        content: "";
        left: -108px;
      }
    }


    @media (min-width: $tablet-width) {
      @include font("Oswald", 400, 36px, 36px);
    }
  }

  &--text {
    @include font("Oswald", 400, 36px, 40px);

    @media (min-width: $tablet-width) {
      @include font("Oswald", 400, 60px, 60px);
    }
  }
}
